import { isNumber, isObject, isString } from 'lodash';
import { JSON_RPC_VERSION } from './constants';
/**
 * Check if value is a RPC notification
 * @param value
 */

export function isJsonRpcNotification(value) {
  return isObject(value) && value.jsonrpc === JSON_RPC_VERSION && isString(value.method) && !value.hasOwnProperty('id');
}
/**
 * Check if value is an RPC request
 * @param value
 */

export function isJsonRpcRequest(value) {
  return isObject(value) && value.jsonrpc === JSON_RPC_VERSION && isString(value.method) && (isString(value.id) || isNumber(value.id));
}
/**
 * Check if value is an RPC error
 * @param value
 */

export function isJsonRpcErrorObject(value) {
  return isObject(value) && isNumber(value.code) && isString(value.message);
}
/**
 * @param obj
 * @param params
 * @private
 */

export function setParams(obj, params) {
  if (isObject(params) || Array.isArray(params)) {
    Object.assign(obj, {
      params: params
    });
  }
}